@import url(https://fonts.googleapis.com/css2?family=Hind:wght@400;500;600;700&display=swap);
html { 
    line-height: 1.15;
    -webkit-text-size-adjust: 100%;
}

body {
    margin: 0;
    padding-bottom: 4em;
    background-color: #373737;
    color: #fcfcfc;
    font-family: 'Hind', sans-serif;
}

main { 
    display: block;
}

h1 {
    font-size: 5em;
}

h2 {
    font-size: 3em;
}

h3 {
    font-size: 2em;
}

p {
    font-size: 1.5em;
}

hr {
    box-sizing: content-box;
    overflow: visible;
    margin: 3em 2em;
    border-top: #585858 solid 2px;
}

button {
    background: linear-gradient(#ED41A8 0%,  #F55E5E 100%);
    color: #fcfcfc;
    border: none;
    border-radius: 10px;
    padding: 0.1em 0.2em;
    transition: background 0.2s;
    font-size: 2em;
}

button:hover {
    background: linear-gradient(#ed61b5 0%, #f27c7c 100%);
}

pre {
    font-family: monospace, monospace;
    font-size: 1em;
}

a {
    background-color: transparent;
}

.base-page-header {
    padding: 0.7em 0 0.5em 0;
}

@media (min-width: 1400px) {
    .base-container-wide {
        max-width: 1340px;
    }
}

img {
    border-radius: 10px;
}

#landing-page {
    padding-top: 5em;
    background-repeat: no-repeat;
}

#landing-align {
    display: inline-block;
}

#landing-colorband {
    position: fixed;
    height: 100%;
    width: 20%;
    background: linear-gradient(#ED41A8 0%, #f55e5e 100%);
    z-index: -1;
    margin-top: -32em;
    margin-left: -2.0em;
}

@media (max-width: 770px) {
    #landing-colorband {
        display: none;
    }
    #landing-main-title {
        background: linear-gradient(#ED41A8 0%, #f55e5e 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }
}

#landing-info-button {
    margin-top: 1.5em;
    
}

#linkedin-logo {
    width: 2.5em;
    height: 2em;
    -webkit-filter: grayscale(100%) brightness(250%);
            filter: grayscale(100%) brightness(250%);
}

#github-logo {
    width: 2em;
    height: 2em;
}

.landing-media {
    margin-top: 1em;
    margin-left: -0.4em;
}

.landing-logo {
    margin: 0 0.5em;
    transition: all 0.2s;
}

.landing-logo:hover {
}

.about-diagonal {
  background: linear-gradient(135deg, #373737 5%, #ED41A8 5%, #f55e5e 23%, #373737 23%);
}

@media (max-width: 768px) {
    .about-diagonal {
        background: none;
    }
}

.about-info-row {
    margin-bottom: 4em;
}

.about-center {
    text-align: center;
}

.about-color-row {
    background: linear-gradient(#ED41A8 0%, #f55e5e 100%);
}

.about-spacing {
    padding: 3em 0;
}

.about-imageframe-base {
    padding: 1em;
    border-radius: 10px;
    background: linear-gradient(#ED41A8 0%, #f55e5e 100%);
}

@media (min-width: 768px) {
    .about-me-image {
        margin-top: -5em;
    }
}

@media (min-width: 1400px) {
    .about-car {
        padding: 0 10em 20em 10em;
    }
}

@media (min-width: 1200px) {
    .about-car {
        padding: 0 8em 16em 8em;
    }
}

.display-overlay {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.2s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 80%;
    height: 70%;
    background-color: #373737;
    border-radius: 10px;
    padding: 0 1.5em; 
    text-align: center;
    height: 75%;
    transform: translate(-50%, -63%);
    -ms-transform: translate(-50%, -63%);
    font-size: 2em;
}

.display-box:hover .display-overlay {
    opacity: 0.9;
}

.display-text {
    color: #f2f2f2;
    font-size: inherit;
}

.display-title {
    text-align: center;
    margin: 0.5em 0 1em 0;
}

@media (max-width: 1400px) {
    .display-text {
        font-size: 0.9em;
    }
    
    .display-overlay {
        transform: translate(-50%, -65%);
        -ms-transform: translate(-50%, -65%);
    }
}

@media (max-width: 1200px) {
    .display-text {
        font-size: 0.7em;
    }
    
    .display-overlay {
        height: 70%;
        transform: translate(-50%, -65%);
        -ms-transform: translate(-50%, -65%);
    }
}

@media (max-width: 992px) {
    .display-text {
        font-size: 0.5em;
    }
    
    .display-overlay {
        height: 65%;
        transform: translate(-50%, -71%);
        -ms-transform: translate(-50%, -71%);
    }
}

@media (max-width: 768px) {
    .display-text {
        font-size: 0.8em;
    }
    
    .display-overlay {
        transform: translate(-50%, -65%);
        -ms-transform: translate(-50%, -65%);
    }
}

@media (max-width: 576px) {
    .display-text {
        font-size: 0.7em;
    }
    
    .display-overlay {
        transform: translate(-50%, -65%);
        -ms-transform: translate(-50%, -65%);
    }
}

@media (max-width: 460px) {
    .display-text {
        font-size: 0.55em;
    }
    
    .display-overlay {
        transform: translate(-50%, -69%);
        -ms-transform: translate(-50%, -69%);
    }
}

@media (max-width: 400px) {
    .display-text {
        font-size: 0.45em;
    }
    
    .display-overlay {
        transform: translate(-50%, -72%);
        -ms-transform: translate(-50%, -72%);
    }
}


@media (max-width: 340px) {
    .display-text {
        font-size: 0.4em;
    }
    
    .display-overlay {
        transform: translate(-50%, -72%);
        -ms-transform: translate(-50%, -72%);
    }
}

@media (max-width: 300px) {
    .display-text {
        font-size: 5vw;
    }

    .display-overlay {
        padding: 5vw 5vw;
        height: 50vw;
        transform: translate(-50%, -40vw);
        -ms-transform: translate(-50%, -40vw);
    }
}

.project-gradient-box {
    position: absolute;
    z-index: -2;
    width: 100%;
    height: 13vw;
    background: linear-gradient(#ED41A8 0%, #F55E5E 80%);
}

.project-curve {
    position: absolute;
    z-index: -1;
}

@media (max-width: 768px) {
    .project-curve {
        display: none;
    }

    .project-gradient-box {
        display: none;
    }
}

.contact-gradient-box {
    position: absolute;
    background: linear-gradient(#ed41a8 0%, #f55e5e 100%);
    width: 100%;
    height: 6.5em;
    z-index: -1;
}

#contact-submit {
    background: linear-gradient(#ed41a8 0%, #f55e5e 100%);
    border: none;
    border-radius: 10px;
    color: #fcfcfc;
    min-width: 7em;
    width: 6%;
    margin-top: 2em;
    font-size: 1.5em;
}

.nav-default-back {
    background-color: #282828;
}

.nav-pink {
    background-color: #ed41a8;
    color: black;
}

.nav-resume {
    color: inherit;
}

.nav-resume:hover {
    color: inherit;
    text-decoration: inherit;
}



